<template>
  <Card>
    <p slot="title">
      添加栏目
    </p>
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="80"
      style="width: 500px"
    >
      <FormItem label="栏目名称" prop="realName">
        <Input v-model="formValidate.realName" placeholder="请输入"></Input>
      </FormItem>
      <FormItem label="菜单排序" prop="sort">
        <Input v-model="formValidate.sort" placeholder="请输入"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')"
          >提交</Button
        >
        <Button style="margin-left: 8px" @click="back">取消</Button>
      </FormItem>
    </Form>
  </Card>
</template>
<script>
import { userUpdate } from '@/api/user.js'
import { SERVER_CODE } from '@/utils/constants.js'

export default {
  data() {
    return {
      formValidate: {
        realName: '',
        sort: null
      },
      ruleValidate: {
        realName: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        sort: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted() {},
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          userUpdate(this.formValidate)
            .then(({ code, msg }) => {
              if (code === SERVER_CODE.CODE_200) {
                this.$Message.success('栏目添加成功！')
                this.$store.state.userStatus = true
                setTimeout(() => {
                  this.$router.go(-1)
                }, 2000)
              } else {
                this.$Message.info(msg)
                console.log('栏目添加失败')
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {})
        }
      })
    },
    back() {
      this.$router.go(-1)
    }
  }
}
</script>
